@use 'mixins';
@use 'variables';
@use 'reset';

body {
  font-family: variables.$font-base;
  font-size: variables.$text-base;
  padding: 0 390px;
  max-width: 1920px;
}

header {
    position: relative;
}

.header {
    display: flex;
    justify-content: space-between;
    padding: 24px 0;
    &__logo {
        display: block;
        width: 161px;
        height: 40px;
        background-color: rgba(196, 196, 196, 1);
    }
}